@mixin OswaldRegular {
  font-family: "Oswald", "Helvetica", sans-serif;
  font-weight: 400;
  font-style: normal;
}

@mixin OswaldMedium {
  font-family: "Oswald", "Helvetica", sans-serif;
  font-weight: 500;
  font-style: normal;
}

@mixin ClearList {
  list-style: none;
  margin: 0;
  padding: 0;
}
